







































































































import comm from '../../static/js/common.js';
export default {
	data() {
		return {
			userInfo: {},
			formData: {
				username: '',
				password: ''
			}
		}
	},
	onLoad() {
		let that = this;
		comm.post(comm.host_gp + "/api/f/user/get").then(function(res) {
			res = res.data;
			that.userInfo = res;
		})
	},
	methods: {
		getCurrVersion() {
			let v = plus.runtime.version;
			console.log(v)
			uni.showToast({
				title: '当前版本:  ' + v || '当前版本:   1.0.1',
				icon: 'none',
				duration: 2000
			});
		},
		resetPwd () {
			uni.navigateTo({
				url: '/pages/resetPwd/resetPwd'
			})
		},
		logout() {
			uni.showModal({
				title: '提示',
				content: '确认退出登陆？',
				success: function(res) {
					if (res.confirm) {
						comm.post(comm.host_gp + "/auth/logout").then(function(res) {
							res = res.data;
							if(res.code == 600) {
								uni.showToast({
									title: '退出成功',
									icon: 'none',
									duration: 2000
								})								
								uni.removeStorage({
									key: 'token',
									success(res) {
										uni.redirectTo({
											url: '/pages/login/login'
										})
									}
								})
							}else {
								uni.showToast({
									title: res.msg,
									icon: 'none',
									duration: 2000
								})
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	}
}
